	export default {
		data() {
			return {
				useUser:this.user,
				myMoney:'0.00'
			}
		},
		onLoad() {
		   
		},
		onShow() {
			this.getMoney()
		},
		methods: {
			cz:function(){
				uni.navigateTo({
					url:'chongzhi/chongzhi'
				})
			},
			tx:function(){
				uni.navigateTo({
					url:'tixian/tixian'
				})
			},
			back : function(){
			   uni.navigateBack({});
			},
			getMoney:function(){
				let that=this;
				uni.showLoading({
					title:'正在查询',
					mask:true
				})
				uni.request({
				    url: that.url1+'/dinghai/user/selectMoney',
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
				    data: {
						uid:that.useUser.uid,
					},
				    success: (res) => {
						if(res.statusCode==200){
							console.log(res);
							if(res.data.flag!=null){
								that.myMoney=res.data.flag.money
							}else{
								that.myMoney='0.00'
							}
							uni.showToast({
								title:'查询成功'
							})
						}else{
							uni.showToast({
								title: '查询失败！请联系客服',
								icon:"none"
							});
						}
				    },
					fail : function(err){
						console.log(err)
						uni.showToast({
							title: '查询失败！原因为：'+err.toString(),
							icon:"none"
						});
					}
				})
			}
		 }
}
